body {
  background: #fffbde;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'shizuru';
  src: local('Shizuru'), url(./fonts/Shizuru.ttf) format('truetype');
}

@font-face {
  font-family: 'copperplate';
  src: local('Copperplate'), url(./fonts/OPTICopperplate-Heavy.otf) format('otf');
}

@font-face {
  font-family: 'permanent maker';
  src: local('Permanent Maker'), url(./fonts/PermanentMarker-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'merriweather';
  src: local('merriweather'), url(./fonts/Merriweather-Bold.ttf) format('truetype');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
